                font-family: palatino linotype,serif !important;
    }
    h3{padding-left: 0.5em !important;}
    #header{
        padding: 0.75em;
        background-color: pink;
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: row;
                flex-direction: row;
        margin: 0 !important;
    }
    .sideTitle{
        color: midnightblue;
    }
    .h4{
        margin: 0!important;
    }
    .h2{
        margin: 0!important;
    }
    .description{
        white-space: pre-wrap;
    white-space: -moz-pre-wrap;
    white-space: -pre-wrap;
    white-space: -o-pre-wrap;
    /* word-wrap: break-word; */
    -webkit-hyphens: auto;
    -ms-hyphens: auto;
    hyphens: auto;
    -ms-word-break: break-all;
    word-break: break-all;
    word-break: break-word;
    -webkit-hyphens: auto;
    -ms-hyphens: auto;
    hyphens: auto;
    }
    .item{
        max-width: 100%;
        padding-left: 2% !important;
    
    }
  }

